export default {
  components: {
    Footer,
    VueScrollFixedNavbar
  },

  data() {
    return {
      open: null
    };
  },

  methods: {
    hideMenu() {
      if (this.$route.path === "/Create" || this.$route.path === "/admin" || this.$route.path === "/home" || this.$route.path === "/NewCanvasMe" || this.$route.path === "/NewCanvasYour" || this.$route.path === "/SetCanvasMe" || this.$route.path === "/SetCanvasYour" || this.$route.path === "/NewArticle" || this.$route.path === "/SetMyBlog") {
