const axios = require("axios");

export default {
  data() {
    return {
      picture: [],
      pictures: [],
      name: null,
      price: null,
      description: null,
      revele: false,
      imageCanvas: null
    };
  },

  mounted: function () {
    axios.get(`/your`).then(res => {
      this.pictures = res.data;
      console.log(this.pictures);
    }).catch(function (error) {
      // handle error
      console.log(error);
    }).then(function () {// always executed
    });
  },
  methods: {
    nameP(picture) {
      console.log(picture.name);
    },

    toggleModale(picture) {
      this.revele = !this.revele;
      this.imageCanvas = picture.picture;
      this.name = picture.name;
      this.price = picture.price;
      this.description = picture.description;
      console.log(picture.picture);
    },

    deleteCanvas(picture) {
      if (confirm("Es-tu sûr de vouloire supprimer ce tableau ?")) {
        axios.delete(`/your/${picture.id}}`).then(res => {
          this.pictures = res.data;
          location.reload();
          this.$router.push("/SetCanvasYour");
        }).catch(function (error) {
          // handle error
          console.log(error);
        });
      } else {
        this.$router.push("SetCanvasYour");
      }
    },

    returnPage() {
      this.$router.push("/create");
    }

  }
};